"use client";

import { useState } from "react";
import Image from "next/image";
import Link from "next/link";

export default function HomeFeed() {
  // Logged-in user role (dynamic later)
  const loggedInUser = {
    Name: "Current User",
    role: "Influencer",
    // Try "Explorer", "Recommender", "Marketing"
  };

  const [posts, setPosts] = useState([
    {
      postId: "1",
      postName: "Summer Fashion 2024",
      description:
        "Latest summer fashion trends with pastel tones & airy outfits.",
      photo: "/background.jpg",
      creator: { Name: "John Doe", role: "Influencer" },
      likesCount: 5,
      feedbacks: [{ id: "c1", userName: "Ariana", comment: "Love this look!" }],
    },
    {
      postId: "2",
      postName: "Streetwear Drop",
      description: "Oversized hoodies, baggy jeans and high-top sneakers.",
      photo: "/background-login.jpg",
      creator: { Name: "Ariana Lee", role: "Explorer" },
      likesCount: 12,
      feedbacks: [],
    },
    {
      postId: "3",
      postName: "Traditional Vibes",
      description: "Elegant Indian traditional outfits perfect for weddings.",
      photo: "/background-register.jpg",
      creator: { Name: "Ravi Sharma", role: "Recommender" },
      likesCount: 9,
      feedbacks: [],
    },
  ]);

  // LIKE FEATURE (NOT for Marketing user & NOT for Marketing posts)
  function handleLike(postId: string) {
    setPosts((prev) =>
      prev.map((p) =>
        p.postId === postId ? { ...p, likesCount: p.likesCount + 1 } : p
      )
    );
  }

  // COMMENT FEATURE (NOT for Marketing posts)
  function addComment(postId: string, text: string) {
    if (!text.trim()) return;

    setPosts((prev) =>
      prev.map((p) =>
        p.postId === postId
          ? {
              ...p,
              feedbacks: [
                ...p.feedbacks,
                {
                  id: Date.now().toString(),
                  userName: loggedInUser.Name,
                  comment: text,
                },
              ],
            }
          : p
      )
    );
  }

  // SHARE FEATURE (Only Influencer + Marketing)
  function sharePost(post: any) {
    if (
      loggedInUser.role === "Explorer" ||
      loggedInUser.role === "Recommender"
    ) {
      alert("You do not have permission to share posts.");
      return;
    }

    alert(`Sharing: ${post.postName}`);
  }

  return (
    <div className="min-h-screen bg-[#F5F5F4] font-[Inter] px-4 py-6 relative">
      {/* Header */}
      <header className="flex justify-between items-center mb-8">
        <h1 className="text-3xl font-bold text-[#1F1F1F]">Pehnava Feed</h1>

        <button
          onClick={logout}
          className="px-4 py-2 bg-[#111827] text-white rounded-lg hover:bg-black transition-all"
        >
          Logout
        </button>
      </header>

      {/* VERTICAL FEED (Pinterest style) */}
      <div className="flex flex-col gap-8 max-w-xl mx-auto pb-24">
        {posts.map((p) => {
          const isMarketingPost = p.creator.role === "Marketing";
          const canShare =
            loggedInUser.role === "Influencer" ||
            loggedInUser.role === "Marketing";

          const canLike = loggedInUser.role !== "Marketing" && !isMarketingPost;

          const canComment =
            loggedInUser.role !== "Marketing" && !isMarketingPost;

          return (
            <div
              key={p.postId}
              className="bg-white rounded-2xl shadow-md overflow-hidden hover:shadow-xl transition-all"
            >
              {/* Image */}
              <div className="relative w-full h-[380px]">
                <Image src={p.photo} fill alt="Post" className="object-cover" />
              </div>

              {/* Content */}
              <div className="p-5 flex flex-col">
                {/* Creator */}
                <div className="flex items-center gap-3 mb-4">
                  <div className="w-12 h-12 bg-[#E8E5E1] rounded-full flex items-center justify-center text-xl font-bold">
                    {p.creator.Name.charAt(0)}
                  </div>

                  <div>
                    <p className="font-semibold text-[#111827]">
                      {p.creator.Name}
                    </p>
                    <p className="text-sm text-gray-500">
                      {p.creator.role === "Marketing"
                        ? "Sponsored"
                        : p.creator.role}
                    </p>
                  </div>
                </div>

                <h2 className="text-xl font-bold">{p.postName}</h2>
                <p className="text-sm text-gray-700 mt-1">{p.description}</p>

                {/* Like / Share buttons */}
                <div className="flex justify-between items-center mt-4">
                  {/* LIKE (only NON-marketing posts) */}
                  {canLike ? (
                    <button
                      onClick={() => handleLike(p.postId)}
                      className="text-[#A27B5C] font-semibold hover:text-[#926B4B]"
                    >
                      ðŸ¤Ž Like
                    </button>
                  ) : (
                    <span className="text-gray-400 text-sm">
                      Likes Disabled
                    </span>
                  )}

                  {/* Likes Count */}
                  {!isMarketingPost && (
                    <span className="text-sm text-gray-600">
                      {p.likesCount} Likes
                    </span>
                  )}

                  {/* SHARE BUTTON â€” Influencers + Marketing Only */}
                  {canShare && (
                    <button
                      onClick={() => sharePost(p)}
                      className="text-[#A27B5C] underline hover:text-[#926B4B]"
                    >
                      Share
                    </button>
                  )}
                </div>

                {/* BUY NOW for Marketing posts */}
                {p.productLink && (
                  <a
                    href={p.productLink}
                    target="_blank"
                    className="mt-4 bg-[#A27B5C] text-white px-4 py-2 rounded-lg text-center hover:bg-[#926B4B]"
                  >
                    Buy Now
                  </a>
                )}

                {/* COMMENTS (Not allowed for Marketing posts) */}
                {canComment && (
                  <div className="mt-5">
                    <p className="font-semibold">Comments:</p>

                    {/* Existing comments */}
                    <div className="mt-2 space-y-2 max-h-32 overflow-y-auto">
                      {p.feedbacks.map((f) => (
                        <div
                          key={f.id}
                          className="bg-[#F7EDE2] p-2 rounded-lg text-sm"
                        >
                          <strong>{f.userName}: </strong>
                          {f.comment}
                        </div>
                      ))}
                    </div>

                    {/* Comment Input */}
                    <div className="flex gap-2 mt-3">
                      <input
                        type="text"
                        placeholder="Add a comment..."
                        className="flex-1 px-3 py-2 border rounded-lg text-sm"
                        onKeyDown={(e) => {
                          if (e.key === "Enter") {
                            addComment(p.postId, e.currentTarget.value);
                            e.currentTarget.value = "";
                          }
                        }}
                      />
                      <button
                        className="px-3 py-2 bg-[#111827] text-white rounded-lg"
                        onClick={(e) => {
                          const input = e.currentTarget
                            .previousSibling as HTMLInputElement;
                          addComment(p.postId, input.value);
                          input.value = "";
                        }}
                      >
                        Post
                      </button>
                    </div>
                  </div>
                )}
              </div>
            </div>
          );
        })}
      </div>

      {/* Floating Create Post (all except Marketing?) */}
      {loggedInUser.role !== "Marketing" && (
        <Link
          href="/create-post"
          className="fixed bottom-6 right-6 bg-[#A27B5C] text-white px-6 py-4 rounded-full shadow-xl hover:bg-[#926B4B]"
        >
          + Create Post
        </Link>
      )}
    </div>
  );
}
